**footer组件**
<template>
  <footer @click="changeTitle">{{txt}}</footer>
</template>
<script>
  export default {
    mounted () {
      this.bus.$on('toChangeTitle', function (title) {
        console.log(title)
      })
    },
    methods: {
      changeTitle () {
        this.bus.$emit('toChangeTitles','尾部')
      }
    },
    data () {
      return {
        txt: '尾部'
      }
    }
  }
</script>



