<template>
  <header @click="changeTitle">{{title}}</header>
</template>
<script>
  export default {
    data () {
      return {
        title: '头部'
      }
    },
    mounted () {
      this.bus.$on('toChangeTitles', function (tex) {
        console.log(tex)
      })
    },
    methods: {
      changeTitle () {
        this.bus.$emit('toChangeTitle','首页')
      }
    }
  }
</script>



